import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { memo, useCallback, useState } from 'react';
import { ControlbarPopoverWrapper, ControlbarWrapper } from './styled';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useControlbarConfig } from '@containers/Home/hook/useControlbarConfig';
import { useSelector } from 'react-redux';
import {
  selectConfigSoundProject,
  selectLanguageProject,
} from '@containers/Home/selectors';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import { Popover, Tooltip } from 'antd';
import { isServer } from '@utils/helper';

export interface Props {
  krpanoRef: Type_RefFuncKrpano | null;
}

export const Controlbar = memo(({ krpanoRef }: Props) => {
  const LANGUAGES = isServer ? {} : window.languages || {};

  const [isShowControlbar, setIsShowControlbar] = useState(true);

  const [{ isSoundOnScene, isActiveModeMultipleStaging }] =
    useTourEventState('state');
  const { onCheckIsShowContentControlbarItem, onRenderContentControlbarItem } =
    useControlbarConfig();

  const languageProject = useSelector(selectLanguageProject);
  const configSoundProject = useSelector(selectConfigSoundProject);

  const {
    //
    onVR,
    onToggleGyro,
    onFullscreen,
    onToFirstScene,
    onViewNormal,
    onViewMirrorBall,
    onViewPlannet,
    onTurnOnScreenshotCapture,
    // NOTE: auto change scene
    isChangeScene,
    isShowIconChangeScene,
    onChangeAutoChangeScene,
    // NOTE: auto rotate
    isRotate,
    isShowIconRotate,
    onToggleRotate,
    // NOTE: sound scene
    isShowIconSoundScene,
    onToggleSoundScene,
    // NOTE: sound project
    isShowIconSoundProject,
    onTurnOffSoundProject,
    onTurnOnSoundProject,
    // NOTE: multiple staging
    isShowIconMultipleStaging,
    onToggleModeMultipleStaging,
    // NOTE: info place
    isShowIconInfoPlace,
    onShowInfoPlace,
    // NOTE: info scene
    isShowIconInfoScene,
    onToggleInfoScene,
    // NOTE: floorplan
    isShowIconFloorplan,
    onToggleFloorplan,
    // NOTE: dollhouse
    isShowIconDollhouse,
    onToggleDollhouseView,
    // NOTE: map
    isShowIconMap,
    onToggleMap,
    // NOTE: multi-language
    languagesProject,
    onChangeLanguageProject,
  } = useKrpano({ krpanoRef });

  const onToggleControlbar = useCallback(
    () => setIsShowControlbar(prev => !prev),
    [],
  );

  return (
    <ControlbarWrapper
      id="themeControlbar"
      className={`${isShowControlbar ? '' : 'inactive'}`}
    >
      <div className="--toggler" onClick={onToggleControlbar}>
        <i className="far fa-ellipsis-vertical"></i>
      </div>
      <div className="controlBar">
        {onCheckIsShowContentControlbarItem('home') && (
          <Tooltip
            title={LANGUAGES[languageProject]?.controlbarFirstScene}
            placement="left"
          >
            <div className="--item" onClick={onToFirstScene}>
              {onRenderContentControlbarItem('home')}
            </div>
          </Tooltip>
        )}
        {isShowIconSoundProject &&
          onCheckIsShowContentControlbarItem('sound_project_off') &&
          onCheckIsShowContentControlbarItem('sound_project_on') && (
            <Tooltip
              title={
                configSoundProject.on
                  ? LANGUAGES[languageProject]?.controlbarTurnOffSoundProject
                  : LANGUAGES[languageProject]?.controlbarTurnOnSoundProject
              }
              placement="left"
            >
              <div
                className="--item"
                onClick={
                  configSoundProject.on
                    ? onTurnOffSoundProject
                    : onTurnOnSoundProject
                }
              >
                {configSoundProject.on
                  ? onRenderContentControlbarItem('sound_project_off')
                  : onRenderContentControlbarItem('sound_project_on')}
              </div>
            </Tooltip>
          )}
        {((isShowIconSoundScene &&
          onCheckIsShowContentControlbarItem('sound_scene_off') &&
          onCheckIsShowContentControlbarItem('sound_scene_on')) ||
          (isShowIconRotate &&
            onCheckIsShowContentControlbarItem('auto_rotate_off') &&
            onCheckIsShowContentControlbarItem('auto_rotate_on')) ||
          (isShowIconChangeScene &&
            onCheckIsShowContentControlbarItem('auto_change_scene_off') &&
            onCheckIsShowContentControlbarItem('auto_change_scene_on'))) && (
          <Popover
            placement="left"
            zIndex={99}
            content={
              <ControlbarPopoverWrapper>
                <ul>
                  {isShowIconSoundScene &&
                    onCheckIsShowContentControlbarItem('sound_scene_off') &&
                    onCheckIsShowContentControlbarItem('sound_scene_on') && (
                      <li>
                        <button type="button" onClick={onToggleSoundScene}>
                          {!isSoundOnScene
                            ? onRenderContentControlbarItem('sound_scene_on')
                            : onRenderContentControlbarItem('sound_scene_off')}
                          {LANGUAGES[languageProject]?.controlbarSoundScene}
                          {/* {isSoundOnScene ? 'Sound Scene' : 'Sound Scene'} */}
                        </button>
                      </li>
                    )}
                  {isShowIconRotate &&
                    onCheckIsShowContentControlbarItem('auto_rotate_off') &&
                    onCheckIsShowContentControlbarItem('auto_rotate_on') && (
                      <li>
                        <button type="button" onClick={onToggleRotate}>
                          {!isRotate
                            ? onRenderContentControlbarItem('auto_rotate_on')
                            : onRenderContentControlbarItem('auto_rotate_off')}
                          {isRotate
                            ? LANGUAGES[languageProject]?.controlbarStopRotate
                            : LANGUAGES[languageProject]?.controlbarStartRotate}
                        </button>
                      </li>
                    )}
                  {isShowIconChangeScene &&
                    onCheckIsShowContentControlbarItem(
                      'auto_change_scene_off',
                    ) &&
                    onCheckIsShowContentControlbarItem(
                      'auto_change_scene_on',
                    ) && (
                      <li>
                        <button
                          type="button"
                          onClick={onChangeAutoChangeScene.bind(
                            this,
                            !isChangeScene,
                          )}
                        >
                          {!isChangeScene
                            ? onRenderContentControlbarItem(
                                'auto_change_scene_on',
                              )
                            : onRenderContentControlbarItem(
                                'auto_change_scene_off',
                              )}
                          {isChangeScene
                            ? LANGUAGES[languageProject]
                                ?.controlbarStopAutoPlayScene
                            : LANGUAGES[languageProject]
                                ?.controlbarStartAutoPlayScene}
                        </button>
                      </li>
                    )}
                </ul>
              </ControlbarPopoverWrapper>
            }
            overlayClassName="popoverControlbar"
          >
            <div className="--item">
              {onRenderContentControlbarItem('group_auto_play')}
            </div>
          </Popover>
        )}

        {onCheckIsShowContentControlbarItem('multi_language') &&
          languagesProject?.length > 1 && (
            <Popover
              placement="left"
              zIndex={99}
              content={
                <ControlbarPopoverWrapper>
                  <ul>
                    {languagesProject.map(item => (
                      <li key={item.id}>
                        <button
                          type="button"
                          onClick={() => onChangeLanguageProject(item.id)}
                        >
                          <img src={item.flag?.src} />
                          {item.name}
                        </button>
                      </li>
                    ))}
                  </ul>
                </ControlbarPopoverWrapper>
              }
              overlayClassName="popoverControlbar"
            >
              <div className="--item">
                {onRenderContentControlbarItem('multi_language')}
              </div>
            </Popover>
          )}

        {onCheckIsShowContentControlbarItem('view_mode') &&
          (onCheckIsShowContentControlbarItem('view_mode_little_planet') ||
            onCheckIsShowContentControlbarItem('view_mode_mirror') ||
            onCheckIsShowContentControlbarItem('view_mode_normal')) && (
            <Popover
              placement="left"
              zIndex={99}
              content={
                <ControlbarPopoverWrapper>
                  <ul>
                    {onCheckIsShowContentControlbarItem('view_mode_normal') && (
                      <li>
                        <button type="button" onClick={() => onViewNormal()}>
                          {onRenderContentControlbarItem('view_mode_normal')}
                          Normal
                        </button>
                      </li>
                    )}
                    {onCheckIsShowContentControlbarItem('view_mode_mirror') && (
                      <li>
                        <button
                          type="button"
                          onClick={() => onViewMirrorBall()}
                        >
                          {onRenderContentControlbarItem('view_mode_mirror')}
                          Mirror Ball
                        </button>
                      </li>
                    )}
                    {onCheckIsShowContentControlbarItem(
                      'view_mode_little_planet',
                    ) && (
                      <li>
                        <button type="button" onClick={() => onViewPlannet()}>
                          {onRenderContentControlbarItem(
                            'view_mode_little_planet',
                          )}
                          Little Planel
                        </button>
                      </li>
                    )}
                  </ul>
                </ControlbarPopoverWrapper>
              }
              overlayClassName="popoverControlbar"
            >
              <div className="--item">
                {onRenderContentControlbarItem('view_mode')}
              </div>
            </Popover>
          )}

        {isShowIconMultipleStaging &&
          onCheckIsShowContentControlbarItem('multi_staging') && (
            <Tooltip
              title={
                isActiveModeMultipleStaging
                  ? LANGUAGES[languageProject]
                      ?.controlbarTurnOffModeMultipleStaging
                  : LANGUAGES[languageProject]
                      ?.controlbarTurnOnModeMultipleStaging
              }
              placement="left"
            >
              <div
                className="--item"
                onClick={() => onToggleModeMultipleStaging()}
              >
                {onRenderContentControlbarItem('multi_staging')}
              </div>
            </Tooltip>
          )}

        {onCheckIsShowContentControlbarItem('gyro') && (
          <div className="--item" onClick={onToggleGyro}>
            {onRenderContentControlbarItem('gyro')}
          </div>
        )}

        {isShowIconInfoPlace &&
          onCheckIsShowContentControlbarItem('info_tour') && (
            <Tooltip
              title={LANGUAGES[languageProject]?.controlbarInfoTour}
              placement="left"
            >
              <div className="--item" onClick={onShowInfoPlace}>
                {onRenderContentControlbarItem('info_tour')}
              </div>
            </Tooltip>
          )}

        {isShowIconInfoScene &&
          onCheckIsShowContentControlbarItem('info_scene') && (
            <Tooltip
              title={LANGUAGES[languageProject]?.controlbarInfoScene}
              placement="left"
            >
              <div
                className="--item"
                onClick={onToggleInfoScene.bind(this, undefined)}
              >
                {onRenderContentControlbarItem('info_scene')}
              </div>
            </Tooltip>
          )}

        {isShowIconDollhouse &&
          onCheckIsShowContentControlbarItem('dollhouse') && (
            <Tooltip
              title={LANGUAGES[languageProject]?.controlbarDollhouse}
              placement="left"
            >
              <div className="--item" onClick={onToggleDollhouseView}>
                {onRenderContentControlbarItem('dollhouse')}
              </div>
            </Tooltip>
          )}

        {isShowIconFloorplan &&
          onCheckIsShowContentControlbarItem('floorplan') && (
            <Tooltip
              title={LANGUAGES[languageProject]?.controlbarFloorplan}
              placement="left"
            >
              <div
                className="--item"
                onClick={onToggleFloorplan.bind(this, undefined)}
              >
                {onRenderContentControlbarItem('floorplan')}
              </div>
            </Tooltip>
          )}

        {isShowIconMap && onCheckIsShowContentControlbarItem('map') && (
          <Tooltip
            title={LANGUAGES[languageProject]?.controlbarMap}
            placement="left"
          >
            <div className="--item" onClick={onToggleMap.bind(this, undefined)}>
              {onRenderContentControlbarItem('map')}
            </div>
          </Tooltip>
        )}

        {onCheckIsShowContentControlbarItem('vr') && (
          <Tooltip
            title={LANGUAGES[languageProject]?.controlbarVRMode}
            placement="left"
          >
            <div className="--item" onClick={onVR}>
              {onRenderContentControlbarItem('vr')}
            </div>
          </Tooltip>
        )}

        {onCheckIsShowContentControlbarItem('screenshot_capture') && (
          <Tooltip
            title={LANGUAGES[languageProject]?.controlbarScreenshotCapture}
            placement="left"
          >
            <div className="--item" onClick={onTurnOnScreenshotCapture}>
              {onRenderContentControlbarItem('screenshot_capture')}
            </div>
          </Tooltip>
        )}

        {onCheckIsShowContentControlbarItem('fullscreen') && (
          <Tooltip
            title={LANGUAGES[languageProject]?.controlbarFullscreen}
            placement="left"
          >
            <div className="--item" onClick={onFullscreen}>
              {onRenderContentControlbarItem('fullscreen')}
            </div>
          </Tooltip>
        )}
      </div>
    </ControlbarWrapper>
  );
});

export default Controlbar;
