import { useCallback, useMemo } from 'react';
import { useSelector } from 'react-redux';
import { selectConfigControlbar, selectSceneSelected } from '../selectors';
import { I_ControlbarKey } from '../types';
import { useTourStats } from '../global/TourStats';
import { DEFAULT_VALUE_CONTROLBAR_CONFIG } from '../constants';

export const useControlbarConfig = () => {
  const [tourStats] = useTourStats('stats');
  const sceneSelected = useSelector(selectSceneSelected);
  const controlbarConfig = useSelector(selectConfigControlbar);

  const onCheckIsShowContentControlbarItem = useCallback(
    (type: I_ControlbarKey) => {
      if (type) {
        const config =
          controlbarConfig[type] || DEFAULT_VALUE_CONTROLBAR_CONFIG[type];

        return type === 'gyro'
          ? !!tourStats.isGyroscopeAvailable && !config?.isHide
          : type === 'vr' || type === 'view_mode'
          ? !config?.isHide &&
            sceneSelected?.media?.subType !== 'flat' &&
            sceneSelected?.type !== 'object360'
          : type.includes('auto_change_scene') ||
            type === 'multi_staging' ||
            type === 'fullscreen' ||
            type === 'dollhouse'
          ? !config?.isHide && sceneSelected?.type !== 'object360'
          : !config?.isHide;
      }
      return true;
    },
    [sceneSelected?.media, sceneSelected?.type, controlbarConfig, tourStats],
  );

  const onRenderContentControlbarItem = useCallback(
    (type: I_ControlbarKey) => {
      if (type) {
        const config =
          controlbarConfig[type] || DEFAULT_VALUE_CONTROLBAR_CONFIG[type];
        if (config.type === 'icon' && config.icon)
          return (
            <i aria-hidden className={`controlbar-item__icon ${config.icon}`} />
          );
        if (config.type === 'image' && config.image?.src)
          return (
            <img
              className="controlbar-item__img"
              src={config.image?.src}
              alt="img"
            />
          );
      }
      return '';
    },
    [controlbarConfig],
  );

  return useMemo(
    () => ({
      onRenderContentControlbarItem,
      onCheckIsShowContentControlbarItem,
    }),
    [onRenderContentControlbarItem, onCheckIsShowContentControlbarItem],
  );
};
